@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensed-Bold.ttf") format("truetype");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "SFSquareHead";
  src: url("../fonts/SFSquareHead.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "SourceSansVariable";
  src: url("../fonts/SourceSansVariable-Roman.otf") format("opentype");
  font-weight: 400;
  font-display: swap;
}
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Montserrat", sans-serif;
  background-color: #ffffff;
  color: #000000;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}

main {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

a {
  color: #000000;
  text-decoration: none;
}

ul,
ol,
nav {
  list-style: none;
  padding: 0;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

/* Blocks */
.graphic {
  width: 100%;
  height: 100%;
  background-color: #403f4d;
  padding: 190px 98px 40px 143px;
}
.graphic__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  width: 100%;
}
.graphic__left {
  width: calc(34.3% - 50px);
}
.graphic__right {
  width: calc(65.7% - 50px);
}

.canvas {
  width: 100%;
  border-radius: 36px;
  overflow: hidden;
  border: 1px solid #ffffff;
}
.canvas__window {
  width: 100%;
  overflow: hidden;
}
.canvas__window canvas {
  overflow: hidden;
}